body, html {
  margin: 0;
  padding: 0;
}



body {
  color: #fff;
  font-size: x-large;
}

.bg {
  height: 100vh;
  background-repeat: no-repeat;
  background-size: cover;
  position: relative;
}

.central-container {
  width: 100%;
  height: 100vh;
  color: "#fff";
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin: auto;
  display: flex;
  position: absolute;
}

.goal-input {
  width: 100%;
  color: #fff;
  text-align: center;
  background: none;
  border: 0;
