.todo-container {
  position: fixed;
  bottom: 0;
  right: 0;
  color: "#fff";
  max-width: 30%;
  display: inline-block;
  text-align: right;
}
.todo-input {
  font-family: inherit;
  width: 100%;
  border: 0;
  border-bottom: 2px solid #fff;
  outline: 0;
  font-size: 1.3rem;
  color: #fff;
  padding: 7px 0;
  background: transparent;
  transition: border-color 0.2s;
  text-align: center;
}
